/*
Loading Spinner
---------------------------------------------*/
.nf-loading-spinner {
  width: 40px;
  height: 40px;
  margin: 15px auto;
  background-color: rgba(208, 208, 208, 0.5);
  border-radius: 100%;
  animation: nf-scaleout 1s infinite ease-in-out;
}

@-webkit-keyframes nf-scaleout {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}
@keyframes nf-scaleout {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}
